import React, { useEffect, useState, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import styled from 'styled-components';
import { useTheme } from '../../context/theme';

const RippleTheme = ({ triggerColor, isActive }) => {
  const [ripples, setRipples] = useState([]);
  const rippleOriginRef = useRef({ x: 50, y: 50 });
  
  useEffect(() => {
    if (isActive && triggerColor) {
      // Get mouse position or use center
      const getRippleOrigin = () => {
        // Try to get from cube hover position (center of screen for cube)
        return { x: 50, y: 50 };
      };
      
      const origin = getRippleOrigin();
      rippleOriginRef.current = origin;
      
      // Create multiple ripples for wave effect (like stone in water)
      const rippleCount = 4;
      const newRipples = [];
      
      for (let i = 0; i < rippleCount; i++) {
        newRipples.push({
          id: Date.now() + i,
          color: triggerColor,
          x: origin.x,
          y: origin.y,
          delay: i * 0.15,
          scale: 1 + i * 0.3,
        });
      }
      
      setRipples(prev => [...prev, ...newRipples]);
      
      // Remove ripples after animation
      setTimeout(() => {
        setRipples(prev => prev.filter(r => !newRipples.find(nr => nr.id === r.id)));
      }, 3000);
    }
  }, [isActive, triggerColor]);
  
  return (
    <RippleContainer>
      <AnimatePresence>
        {ripples.map((ripple) => (
          <Ripple
            key={ripple.id}
            initial={{ scale: 0, opacity: 0 }}
            animate={{ 
              scale: [0, ripple.scale, ripple.scale * 1.5, ripple.scale * 2.5, ripple.scale * 4],
              opacity: [0, 0.95, 0.75, 0.5, 0.25, 0],
            }}
            exit={{ opacity: 0 }}
            transition={{
              duration: 2.8,
              delay: ripple.delay,
              ease: [0.16, 1, 0.3, 1],
              opacity: {
                times: [0, 0.05, 0.2, 0.5, 0.8, 1],
              },
              scale: {
                times: [0, 0.15, 0.35, 0.6, 1],
              },
            }}
            $color={ripple.color}
            style={{
              left: `${ripple.x}%`,
              top: `${ripple.y}%`,
            }}
          />
        ))}
      </AnimatePresence>
    </RippleContainer>
  );
};

const RippleContainer = styled.div`
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 9998;
  overflow: hidden;
`;

const Ripple = styled(motion.div)`
  position: absolute;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: radial-gradient(
    circle,
    ${({ $color }) => $color}E6 0%,
    ${({ $color }) => $color}CC 10%,
    ${({ $color }) => $color}99 20%,
    ${({ $color }) => $color}66 35%,
    ${({ $color }) => $color}33 50%,
    ${({ $color }) => $color}11 70%,
    transparent 100%
  );
  transform: translate(-50%, -50%);
  filter: blur(40px);
  mix-blend-mode: screen;
  will-change: transform, opacity;
  box-shadow: 0 0 100px ${({ $color }) => $color}80;
`;

export default RippleTheme;

